  max-width: 800px;
  margin: 0 auto;
  position: relative;
  background: #fff;
  border-radius: 6px 6px 3px 3px;
  padding: 20px 2%;
  margin-bottom: 50px;

  *{
    user-select: text !important;
  }

  .text_body{
    text-align: left;
    width: 100%;

    .text_list{
      padding: 5px 10px;
      margin-bottom: 50px;
      
      .title{

        text-align: left;
        font-size: 22px;
      }

      .introduction_regular,
      .introduction{
        padding: 10px;
        font-size: 15px;
        text-align: left;
        background: #282c34;
        color: #ff9401;
      }

      .introduction_regular{
        padding: 15px;
      }
    }
    .markdown{
      .lang-javascript{
        
      }
    }
  }
}
</style>
